body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.headerPoint{
    float: right;
    width: 150px;
    height: 100%;
    text-align: center;
    border-left: 1px solid #BC9321;
}

.navPoint{
    font-size: 16px;
    color: #ffffff;
    font-weight: bold;
    vertical-align: middle;
    line-height: 150px;
    display: block;
    text-decoration: none;
}
.navPoint:hover{
    color: #BC9321;
}
.header{
    height: 150px;
    width: 100%;
    border-bottom: 1px solid #BC9321;
    background-color: #000000;
}
@media only screen and (max-width: 1035px) {
    .header {
        display: none;
    }
}

.homeButton{
    width: 260px;
    float: left;
    display: block;
    text-decoration: none;
}

.logo{
    height: 140px;
    margin-top: 5px;
    margin-left: 5px;
    float: left;
}

.logoName{
    width: 100px;
    float:right;
    margin-top: 48px;
    margin-right: 10px;
}
.welcome{
    margin-bottom: 40px;
    width: 100%;
    height: calc(100vh - 151px);
    display: table;
    z-index: 0;
    position: relative;
    overflow: hidden;
}

.welcomeImgContainer{
    display: block;
    z-index: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
}

.welcomeImgContainer > img {
    object-fit: cover;
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    vertical-align: bottom;
}

.welcomeTextContainer{
    position: absolute;
    z-index: 10;
    width: 100%;
    text-align: center;
    height: 50%;
    top: calc(50% - 10px);
    font-size: 20px;
    color: #bc9321;
}

.welcomeText{
    width: 198px;
    padding: 15px;
    background-color: rgba(0, 0, 0, 0.9);
    display: inline-block;
}
.sectionTitle{
    text-align: center;
    width: 100%;
    margin-bottom: 10px;
}

.section{
    width: 100%;
}

.standardSection{
    margin-bottom: 50px;
}
.ueberUnsTextContainer{
    width: 52.5%;
    padding-left: 7.5%;
    float: left;
    margin-top: 13%;
}

.ueberUnsTextTitle{
    font-size: 16px;
    font-weight: bold;
    color: #ffffff;
}

.ueberUnsText{
    color: #ffffff;
    margin-top: 10px;
}

.ueberUnsImgContainer{
    float: left;
    width: 40%;
}

.ueberUnsImgContainer > img {
    width: 60%;
    float: right;
    margin-right: 18.6%;
}
.tourWrapper{
    width: 100%;
    height: 100%;
    text-align: center;
}

.videoWrapper{
    width: 85%;
    display: inline-block;
    margin-bottom: 60px;
}
.angebotContainer{
    overflow: hidden;
    position: relative;
    width: 100%;
}

.angebotName{
    color: #FFFFFF;
}

.angebotPreis{
    color: #BC9321;
    padding-left: 30px;
}

.angebotTable{
    width: 100%;
    padding-left: 15%;
    padding-right: 40px;
    margin-top: 100px;
    margin-bottom: 100px;
}
.startNewTableSection{
    margin-top: 10px;
}

.angebot{
    width: 50%;
    float: left;
}

.preislisteContainer{
    width: 50%;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    text-align: right;
}

.preisliste{
    height: 100%;
    margin-right: 15%;
}
.kontaktAnkunft{
    color: #ffffff;
    float: left;
    width: 30%;
    min-width: 200px;
}

.thirdTitle{
    font-size: 16px;
    font-weight: bold;
}

.telefon{
    margin-left: 25%;
    margin-top: 160px;
}
.adresse{
    margin-left: 25%;
    margin-top: 10px;
}

.mapsContainer{
    float: left;
    width: 70%;
}
.oeffnungszeitenTable{
    color: #ffffff;
    width: 100%;
    padding-right: 40px;
    padding-left: 12.5%;
    margin-top: calc(25% - 60px);
}

.oeffnungszeitenContainer{
    position: relative;
}

.oeffnungszeiten{
    width: 60%;
    float: left;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
}

.oeffnungszeitenBild{
    width: 40%;
    float: right;
}

.oeffnungszeitenBild > img {
    width: 100%;
}
footer{
    height: 120px;
    background-color: #000000;
    color: #ffffff;
    padding: 20px;
}

.findUsAt{
    float: left;
}

.findUsAtText{
    font-weight: bold;
    display: block;
}

.socialMedia{
    height: 30px;
    line-height: 30px;
    display: block;
    color: #ffffff;
    margin-top: 15px;
}

.socialMedia:visited{
    color: #ffffff;
}

.socialIconContainer{
    display: inline-block;
    height: 100%;
}
.socialIcon{
    height: 100%;
    margin-right: 5px;
}

.socialLink{
    display: inline-block;
    vertical-align: top;
    margin-top: 3px;
    line-height: normal;
}


.footerRightInfo{
    float: right;
}
.app{
  background-color: #282727;
}

.titleText{
  font-size: 20px;
  color: #BC9321;
}

body{
  background-color: #282727;
}
